* {
  margin: 0;
  scroll-behavior: smooth;
}
body{
cursor: url("./images/cursor.png"),auto;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.greenColor {
  color: green !important;
}
.redColor {
  color: red !important;
}

.quantityBtn > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: #fff;
  transition: all 0.5s;
}

.quantityBtn > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.quantityBtn > input {
  border: none;
  padding: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  color: rgba(0, 0, 0, 0.74);
  font: 400 0.8vmax "Roboto";
}

.dashboard {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.dashboard > div:last-child {
  border-left: 1px solid rgba(0, 0, 0, 0.13);
  background-color: #fff;
  padding: 2rem 0;
  margin-left: 230px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
}

@media screen and (max-width: 600px) {
  .quantityBtn > button {
    padding: 1.5vmax;
  }
  .quantityBtn > input {
    padding: 1.5vmax;
    font: 400 1.8vmax "Roboto";
  }

  *::-webkit-scrollbar {
    width: 5px;
  }

  .dashboard {
    grid-template-columns: 1fr;
  }
  .dashboard > div:last-child {
    position: relative;
    margin-left: 0;
    height: 50%;
  }
}
