footer {
    display: flex;
    align-items: center;
    background-color: rgb(34, 34, 33);

    color: #fff;
    margin-top: 10vmax;
    padding: 2vmax;

}


.leftFooter {

    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.leftFooter>h4 {
    font-family: "Roboto";
    font-size: 1vmax;

}

.leftFooter>p {
    text-align: center;
    font-size: 1.2vmax;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;

}

.leftFooter>img {
    width: 10vmax;
    margin: 1vmax;
    cursor: pointer;
}

.middleFooter {
    width: 60%;
    text-align: center;
}

.middleFooter>h1 {
    font-size: 4vmax;
    font-family: "Robota";
    color: #eb4034;
}

.midFooter>p {
    max-width: 60%;
    margin: 1vmax auto;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.rightFooter {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightFooter>h4 {
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-size: 1.4vmax;
    text-decoration: underline;
}

.rightFooter>a {
    text-decoration: none;
    font-size: 1.3vmax;
    color: #fff;
    transition: all 0.5s;
}

.rightFooter>a:hover {
    color: #eb4034;
}