.loader {
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    display: grid;
    place-items: center;

    max-width: 100%;
}

.loader>div {
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid rgb(110, 110, 110);
    border-radius: 50%;
    animation: rotating 800ms linear infinite;
}

@keyframes rotating {
    from {
        border-bottom: 5px solid rgb(110, 110, 110);
    }

    to {
        border-bottom: 4px solid rgb(110, 110, 110);
        transform: rotateZ(-360deg);
    }
}